"use client";
import { Account } from '@/types';
import { Wallet, Building2, Banknote, CreditCard } from 'lucide-react';
import { cn } from '@/lib/utils';

export default function RealisticAccountCard({ account }: { account: Account }) {
  // Icono dinámico
  const Icon = account.type === 'wallet' ? Wallet : 
               account.type === 'cash' ? Banknote : 
               account.type === 'bond' ? CreditCard : Building2;

  // Color de borde y cuadrado basado en la elección del usuario
  const cardColor = account.color || '#52525b'; // Zinc-600 default

  return (
    <div 
        className="relative overflow-hidden rounded-xl p-4 bg-zinc-900 border border-zinc-800 shadow-md transition-all hover:scale-[1.02]"
        style={{ borderLeft: `4px solid ${cardColor}` }}
    >
      <div className="flex justify-between items-start mb-3">
        <div className="flex items-center gap-3">
            {/* Cuadro de color pequeño */}
            <div className="w-3 h-3 rounded-sm shadow-[0_0_10px_rgba(0,0,0,0.5)]" style={{ backgroundColor: cardColor }} />
            
            {/* Nombre de la Cuenta (GRANDE) */}
            <div>
                <h3 className="text-lg font-bold text-white leading-tight">{account.name}</h3>
                {/* Detalles (Pequeño) */}
                <p className="text-xs text-zinc-500 font-medium">
                    {account.bank} • <span className="text-zinc-400">{account.owner}</span>
                </p>
            </div>
        </div>
        <Icon className="text-zinc-600 opacity-50" size={20} />
      </div>

      <div className="flex justify-end items-end">
         <div className="text-right">
            <p className="text-[10px] text-zinc-600 uppercase tracking-wider font-bold">Saldo Actual</p>
            {/* Valor con color condicional */}
            <p className={cn(
                "text-xl font-mono font-bold tracking-tight",
                account.balance >= 0 ? "text-emerald-400" : "text-rose-400"
            )}>
                ${account.balance.toLocaleString('es-CO')}
            </p>
         </div>
      </div>
    </div>
  );
}