"use client";
import { useState } from 'react';
import { useFinance } from '@/context/FinanceContext';
import RealisticAccountCard from './RealisticAccountCard';
import { Button } from '@/components/ui/button';
import { ChevronRight, ChevronLeft, Wallet } from 'lucide-react';
import { cn } from '@/lib/utils';

interface Props {
    className?: string;
}

export default function AccountsSidebar({ className }: Props) {
  const { accounts } = useFinance();
  const [isOpen, setIsOpen] = useState(false); // Por defecto cerrado en móvil/desktop para no estorbar
  const visibleAccounts = accounts.filter(a => !a.isHidden);
  const totalBalance = visibleAccounts.reduce((sum, acc) => sum + acc.balance, 0);

  return (
    <>
      {/* Botón Flotante para abrir/cerrar */}
      <Button 
        variant="default" 
        size="icon"
        onClick={() => setIsOpen(!isOpen)}
        className={cn(
            "fixed top-24 right-0 z-50 rounded-l-xl rounded-r-none shadow-xl transition-all duration-300 bg-indigo-600 hover:bg-indigo-700 w-8 h-12 border-l border-y border-indigo-400",
            isOpen ? "mr-[320px]" : "mr-0"
        )}
      >
        {isOpen ? <ChevronRight /> : <Wallet className="h-4 w-4" />}
      </Button>

      {/* Sidebar Overlay */}
      {/* Fondo oscuro para cerrar al hacer click fuera (solo móvil) */}
      {isOpen && (
        <div 
            className="fixed inset-0 bg-black/50 backdrop-blur-sm z-40 md:hidden" 
            onClick={() => setIsOpen(false)}
        />
      )}

      <aside className={cn(
          // Clases base
          "fixed top-0 bottom-0 right-0 z-50 w-[320px] bg-zinc-950 border-l border-zinc-800 shadow-2xl transition-transform duration-300 ease-in-out flex flex-col",
          // Lógica de apertura
          isOpen ? "translate-x-0" : "translate-x-full",
          // 3. AQUÍ fusionamos la clase que viene desde layout ('h-full')
          className 
      )}>
        <div className="p-6 border-b border-zinc-900 bg-zinc-950">
            <h2 className="text-xl font-bold text-zinc-100 flex items-center gap-2">
                <Wallet className="text-indigo-500" /> Mis Cuentas
            </h2>
            <div className="mt-4 p-4 bg-zinc-900/50 rounded-xl border border-zinc-800/50 text-center">
                <p className="text-xs text-zinc-500 uppercase tracking-widest">Balance Total</p>
                <p className={cn("text-2xl font-extrabold mt-1", totalBalance >= 0 ? "text-emerald-400" : "text-rose-400")}>
                    ${totalBalance.toLocaleString('es-CO')}
                </p>
            </div>
        </div>

        <div className="flex-1 overflow-y-auto p-4 space-y-3 pb-20 scrollbar-thin scrollbar-thumb-zinc-800">
          {visibleAccounts.length === 0 && <p className="text-zinc-500 text-center text-sm">No hay cuentas visibles.</p>}
          {visibleAccounts.map(acc => (
            <RealisticAccountCard key={acc.id} account={acc} />
          ))}
        </div>
      </aside>
    </>
  );
}